
.carousel {
  width: 550px;
  height: 240px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.arrow {
  margin: 10px;
  width: 35px;
  height: 35px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  outline: none;
  border: 1px solid grey;
  border-radius: 20px;
  cursor: pointer;
}
.itemsList {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.card {
  width: 200px;
  height: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid grey;
}
.default{
  background-color: white;
}
.card.correct {
  background-color: lightgreen;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.incorrect {
  background-color: pink;
}
.update{
  margin-top: -8px;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-flex: 0.7;
      -ms-flex-positive: 0.7;
          flex-grow: 0.7;
  height: 30px;
  cursor: pointer;
}
.update.error{
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
  margin-left: 20px; 
  margin-right: 0;
}
.popup-invitation{
  position: absolute;
  z-index: 1;
  top: 50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 20px;
  width: 400px;
  border: solid 3px #9dabc6;
  border-radius: 10px;
  box-shadow: 1px 10px 106px -15px rgba(0,0,0,0.75);
  background-color: white;
  color: #FBC02D;
  text-align: center;
  font-family: "Arial", sans-serif;
}
.popup-invitation .cross{
  position: absolute;
  top: -15px;
  right: -15px;
  padding: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #7291b0;
  color:white;
  line-height: 30px;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
}
.popup-invitation a img{
  width: 100px;
}
.popup-invitation a img:hover{
  -webkit-filter: opacity(0.9);
          filter: opacity(0.9);
}



